<template>
  <div>
    <home-header></home-header>
    <home-swiper :list="swiperList"></home-swiper>
    <home-icons :list="iconList"></home-icons>
    <home-Recommend :list="recommendList"></home-Recommend>
    <home-Weekend :list="weekendList"></home-Weekend>
  </div>
</template>

<script>
import HomeHeader from "./components/Header";
import HomeSwiper from "./components/Swiper";
import HomeIcons from "./components/Icons";
import HomeRecommend from "./components/Recommend";
import HomeWeekend from "./components/Weekend";
import axios from "axios";
import { mapState } from "vuex";

export default {
  name: "Home",
  components: {
    HomeHeader,
    HomeSwiper,
    HomeIcons,
    HomeRecommend,
    HomeWeekend
  },
  data() {
    return {
      lastCity: "",
      swiperList: [],
      iconList: [],
      recommendList: [],
      weekendList: []
    };
  },
  computed: {
    ...mapState(["city"])
  },
  methods: {
    getHomeInfo() {
      axios
        .get("https://13816455561.github.io/dist/index.json?city=" + this.city)
        .then(this.getHomeInfoSucc);
    },
    getHomeInfoSucc(res) {
      res = res.data;
      if (res.ret && res.data) {
        const data = res.data;
        this.swiperList = data.swiperList;
        this.iconList = data.iconList;
        this.recommendList = data.recommendList;
        this.weekendList = data.weekendList;
      }
    }
  },
  mounted() {
    this.lastCity = this.city;
    this.getHomeInfo();

    const config = {
      headers: {
        "Content-Type": "application/json"
      }
    };
    let params = {
      "PackageData": {
        "pageindex": 2,
        "pagesize": 20,
        "keywords": "",
        "bid": "",
        "pid_n": 0,
        "pid_m": 0,
        "tid": "",
        "soid": 0,
        "kid_m": "",
        "kid_n": "",
        "commtype": "",
        "levelid": "",
        "provinceid": "",
        "usertype": "",
        "tagid": "",
        "tagtype": "",
        "brandtagid": "",
        "channelId": "1"
      },
        "TransactionID": "201903081312121111111",
        "RequestDate": "20190308131212"
    } 
    this.$http
      .post("/api/commodity/list",JSON.stringify(params),config)
      .then(res => {
        console.log(res);
      })
      .catch(err => {
        console.log(err);
      });
  },
  activated() {
    if (this.lastCity !== this.city) {
      this.lastCity = this.city;
      this.getHomeInfo();
    }
  }
};
</script>

